import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: ()=>import('@/components/Main')
    },
    {
      path: '/dataStructureDefinition',
      name: 'dataStructureDefinition',
      component: ()=>import('@/components/dataStructureDefinition')
    },
    {
      path: '/dataGenrator',
      name: 'dataGenrator',
      component: ()=>import('@/components/dataGenrator')
    },
    {
      path: '/T3',
      name: 'T3',
      component: ()=>import('@/components/T/T3')
    },
    {
      path:'/T5',
      name:'T5',
      component: ()=>import('@/components/T/T5')
    },
    
    {
      path:'/T8',
      name:'T8',
      component: ()=>import('@/components/T/T8')
    },
    {
      path:'/T9',
      name:'T9',
      component: ()=>import('@/components/T/T9')
    },
    {
      path: '/team/:name',
      name: 'team',
      component: ()=>import('@/components/teammates')
    },
    {
      path: '/taskList',
      name: 'taskList',
      component: ()=>import('@/components/Tasklist')
    },
    {
      path: '/dataTable',
      name: 'dataTable',
      component: ()=>import('@/components/DataTable')
    },
    // {
    //   path: '/dataTable',
    //   name: 'dataTable',
    //   component: ()=>import('@/components/DataTable')
    // }
    
  ]
})

