<template>
    <mavon-editor v-model="getTxt" :editable="false" :toolbarsFlag="false" :subfield="false" defaultOpen="preview">
    </mavon-editor>
</template>


<script>
export default {
    name: "MarkdownReader",
    props: ['url'],
    data() {
      
      return {
          text:'',
        }
    },
    methods:{
      getText:function(){
          this.$axios.get(`/static/${this.url}.md`).then((resp)=>{
          this.text = resp.data
        })}
    },
    computed:{
      getTxt:function(){
        this.getText()
        return this.text
      }
    }
}
</script>


