<template> <el-container>
  <el-tabs v-model="activeName" >
    <el-tab-pane label="算法实现" name="first">
      <MarkdownReader :url="url"></MarkdownReader>
    </el-tab-pane>
    <el-tab-pane label="算法调用" name="second" >
         <el-table :data="books">
            <el-table-column prop="bookId" label="书号" width="120">
            </el-table-column>
            <el-table-column prop="bookName" label="书名" width="140">
            </el-table-column>
            <el-table-column prop="author" label="作者">
            </el-table-column>
            <el-table-column prop="monthTicket" label="月票">
            </el-table-column>
         </el-table>
    </el-tab-pane>
    <el-tab-pane name="third" disabled>
      <span slot="label">
        
         <el-select v-model="bookFile" placeholder="请选择输入文件" @change="openBooksFile" >
         <el-option
            v-for="file in files"
            :key="file"
            :label="file"
            :value="file" >
         </el-option>
         </el-select>
      </span>
    </el-tab-pane>
  </el-tabs>
  </el-container>

</template>

<script>
export default {
   name:"DataTable",
   data(){

      return {
         activeName:"first",
         url:"dataTable",
         files:[],
         bookFile:"",
         books:[]

      }
   },
   created(){
      this.$axios.get('/api/listJson').then((res)=>{
         this.files = res.data
      }).catch(()=>{})

   },
   mounted(){
            if(window.location.hash.split('file=').length==2){
         this.bookFile = window.location.hash.split('file=')[1],
         this.activeName = "second"
         this.openBooksFile()
      }
   },
   methods:{
      openBooksFile:function(){
         console.log(this.bookFile)
         this.$axios.get('/static/json/'+this.bookFile).then((res)=>{
         this.books = res.data
         // console.log(res.data)
      })
      return this.books
      }
   }
}
</script>


